/*Styles for Homepage*/

.home {
  background-image: url("images/home-background.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  height: 100vh;
  max-height: 100%;
}

.title {
  margin-top: 6vh;
  font-size: 6vh;
  padding: 10px;
  color: rgba(255, 255, 255, 0.7);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  margin-bottom: 10vh;
}

.aboutMe {
  display: grid;
  grid-template-columns: 1fr;
  background-color: rgb(0, 0, 0,.8);
  border-radius: 5px;
  width: 90vw;
  align-items: left;
  color: white;
  padding: 2vh;
}

.buttons-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 5vh;
}

.linked-in-container {
  perspective: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.linked-in-container .linked-in {
  width: 20vh;
  height: 30vh;
  position: relative; /* Change to relative */
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.linked-in-container .linked-in:hover {
  transform: rotateY(-180deg);
}

.linked-in-front,
.linked-in-back {
  border-radius: 2vh;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background-position: center; /* Ensure background is centered */
  background-size: cover;
}

.linked-in-front {
  font-size: 4vh;
  color: white;
  background-image: url("images/linkedIn.png");
  background-color: black;
  background-repeat: no-repeat;
  background-size: contain;
}

.linked-in-back {
  background-image: url("images/myFace.jpg");
  border: solid;
  border-color: black;
  transform: rotateY(180deg);
}

.linked-in-back a {
  width:100%;
  height:100%;
  background-color: white;
}

.resume-container {
  perspective: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.resume-container .resume {
  width: 20vh;
  height: 30vh;
  position: relative;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.resume-container .resume:hover {
  transform: rotateY(180deg);
}

.resume-front,
.resume-back {
  border-radius: 2vh;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background-position: center; /* Ensure background is centered */
  background-size: cover;
}

.resume-front {
  font-size: 4vh;
  color: white;
  background-image: url("images/resume.png");
  background-color: black;
  background-repeat: no-repeat;
  background-size: contain;
}

.resume-back {
  background-image: url("images/myResume.png");
  border: solid;
  border-color: black;
  transform: rotateY(180deg);
}

.resume-back a {
  width:100%;
  height:100%;
}
